function Portfolios() {
  const [dataVisibleCount, setDataVisibleCount] = useState(6);
  const [dataIncrement] = useState(3); // This could also be a constant outside the component if it doesn't change.
  const [visibleItems, setVisibleItems] = useState([]);
  const [noMorePost, setNoMorePost] = useState(false);

  useEffect(() => {
    setVisibleItems(allData.slice(0, dataVisibleCount)); // Directly use allData here.
  }, [dataVisibleCount]); // Depend on dataVisibleCount to re-run the effect when it changes.

  const handleLoadmore = (e) => {
    e.preventDefault();
    let tempCount = dataVisibleCount + dataIncrement;

    if (tempCount >= allData.length) {
      tempCount = allData.length; // Adjust tempCount to avoid exceeding the array length.
      setNoMorePost(true);
    }

    setDataVisibleCount(tempCount); // Update the count to trigger the useEffect.
  };

  return (
    <>
      <div className="row portfolio-wrapper">
        {visibleItems.map((item) => (
          <div className="col-md-6 col-sm-6 grid-item" key={item.id}>
            <Portfolio portfolio={item} />
          </div>
        ))}
      </div>

      {/* {!noMorePost && (
        <div className="load-more text-center mt-4">
          <button
            className="btn btn-default"
            onClick={handleLoadmore}
          >
            <i className="fas fa-circle-notch"></i> Load more
          </button>
        </div>
      )} */}
    </>
  );
}

export default Portfolios;