import React from "react";

function Experience({ experience: { year, degree, content, company, place } }) {
  return (
    <div className="entry">
      <div className="title">
        <span>{place}</span>
        <br></br>
        <span>{year}</span>
      </div>
      <div className="body">
        <h4 className="mt-0">{company}</h4>
        <h4 className="mt-0">{degree}</h4>
        <ul>
          {content
            .split("•")
            .slice(1)
            .map((item, index) => (
              <li key={index}>{item.trim()}</li>
            ))}
        </ul>
      </div>
    </div>
  );
}

export default Experience;
